import React, { useState } from 'react';
import Layout from '../components/layout';
// Gatsby supports TypeScript natively!
import { PageProps, Link, graphql } from "gatsby"

import WorksSection from '../components/WorksSection';
import DisplayTag from '../components/DisplayTag';
import PropTypes from "prop-types"

const TagsPage = ({
    data: {
        allWorksYaml: { edges, group },
        site: {
            siteMetadata: { title },
        },
    },
}) => {
    const [filter, setFilter] = useState('')
    const handleClick = (e) => {
        if (!e.target || !e.target.textContent) console.log('nothing selected');
        const str = e.target.textContent;
        const tag = str.substr(0, str.indexOf(' ('))
        setFilter(tag);
    }

    return (
        <div>
            <Layout title={title}>
                <DisplayTag clickHandler={handleClick} group={group} />
                <WorksSection edges={filter ? edges.filter((elem) => elem.node.tags.includes(filter)) : edges || []} />
            </Layout>
        </div>
    )
}


TagsPage.propTypes = {
    data: PropTypes.shape({
        allWorksYaml: PropTypes.shape({
            edges: PropTypes.arrayOf(
                PropTypes.shape({
                    id: PropTypes.string,
                })
            ),
        }),
        site: PropTypes.shape({
            siteMetadata: PropTypes.shape({
                title: PropTypes.string.isRequired,
            }),
        }),
    }),
}

export default TagsPage

export const pageQuery = graphql`
    query {
        site {
            siteMetadata {
                title
            }
        }
        allWorksYaml {
            group(field: tags) {
                fieldValue
                totalCount
            }            
            edges {
                node {
                    title
                    tags
                    image {
                        id
                        childImageSharp {
                          fluid {
                            ...GatsbyImageSharpFluid
                            originalImg
                        }
                    }                  
                }
            }
        }
    }
}`
